<template>
    <div class="wrap" >
        <div class="left">
            <ul>
                <li v-for="(item,index) in list" :key="index" :class="[activeindex==index?'active':'']" @click="onclick(index)">{{ item }}</li>
            </ul>
        </div>
        <div class="right"  ref="right" >
            <div v-for="(item,index) in list" :key="index" ref="title">
                <span>{{ item }}</span>
                <!--<span>121232323</span>-->
            </div>
        </div>

    </div>

</template>

<script>
    export default {
        name: "mobile-left-right",
        data(){
            return{
               list:[1,2,3,4,6],
                activeindex:0
            }
        },
        mounted(){
            window.addEventListener('scroll',this.onScroll,true);
            // document.addEventListener("scroll", this.onScroll, false)
        },
        methods:{
            onScroll(e){
                let res=e.target.scrollTop;
                var index = Math.ceil(res/300)
                this.activeindex = index
                if(index<0){
                    this.activeindex = 0
                }else if(index>this.list.length-1){
                    this.activeindex = this.list.length-1
                }

            },
            onclick(index){
                // this.$refs.right.scrollTop = index*300
                document.querySelector(".right").scrollTop = index*300

            }
        }
    }
</script>

<style scoped>
    .wrap{
        /*position: fixed;*/
        /*top: 0;*/
        /*left: 0;*/
        /*right:0;*/
        /*bottom: 0;*/
        margin: 0;
        padding: 0;
        overflow:hidden;
    }
    .left{
        width: 20%;
        position: fixed;
        left: 0;
        top: 0;
        bottom: 0;
        background: red;
    }
    .right{
        position: fixed;
        width: 80%;
        margin-left: 20%;
        background: green;
        top: 0;
        right:0;
        bottom: 0;
        /*overflow: auto;*/
        overflow-y: scroll;
        overflow-x: hidden;
        /*height: 500px;*/
        /*height: 100vh;*/
    }

    .right div{
        height: 300px;
        background: green;
        color: #fff;
        text-align: center;
        font-size: 24px;
        width: 100%;
    }
    .left ul{
        width: 100%;
    }
    .left ul li{
        width: 100%;
        height: 50px;
        color: #fff;
        font-size: 24px;
        text-align: center;
    }
    .left ul li.active{
        background:blue;
    }
    ::-webkit-scrollbar{
        width: 3px!important;
        height: 3px!important;
        background-color:rgba(225,225,225,0)!important;
    }

</style>
